import { Layout } from '@consta/uikit/Layout';
import { Text } from '@consta/uikit/Text';
import styles from './styles.module.css';
import { Menu } from './Menu/Menu';
import { Chat } from './Chat/Chat';
import { Chats } from './Chats/Chats';
import { useState } from 'react';

export const MainPage = ({ hideMain }) => {
  const [visibility, setVisibility] = useState(false);

  function toggleVisibility() {
    setVisibility(!visibility);
  }

  function hideBlock() {
    if (visibility) return { display: 'none' };
    return { display: 'block' };
  }

  return (
    <div style={hideBlock()}>
      {console.log(visibility)}
      <Layout direction="row" className={styles.mainLayout}>
        <Layout direction="column" flex={1} className={styles.sideBar}>
          <Layout flex={1}>
            {/* <div className={styles.chats}> */}
            <Chats />
            {/* </div> */}
          </Layout>
          <Layout flex={1}>
            <Menu />
          </Layout>
        </Layout>
        <Layout flex={3} className={styles.chats}>
          <Chat />
        </Layout>
      </Layout>
    </div>
  );
};
