import styles from './styles.module.css';
import { Text } from '@consta/uikit/Text';
import axios from 'axios';
import { TextField } from '@consta/uikit//TextField';
import { Button } from '@consta/uikit/Button';
import { IconSendMessage } from '@consta/uikit/IconSendMessage';
import React, { useReducer, useState, useRef, useEffect } from 'react';
import { ChatRow } from './ChatRow/ChatRow';

let nextId = 0;
var height = 0;

export const Chat = () => {
  // const apiUrl = 'http://146.185.234.11:8000/chat/';
  const apiUrl = 'http://146.185.234.11/back/';

  const [value, setValue] = useState('');
  const [messages, setMessages] = useState([]);
  const handleChange = ({ value }) => setValue(value);

  function sendRequest() {
    const data = {
      user_id: 'mion_user',
      payload: value
    };
    //setMessages([...messages, { id: nextId++, message: value }]);
    setMessages((messages) => [
      ...messages,
      { id: nextId++, speaker: 'user', message: value }
    ]);
    // console.log(messages);
    setValue(null);
    // console.log(data.payload);
    // let url = apiUrl + value;
    axios.post(apiUrl, data).then((response) => {
      setMessages((messages) => [
        ...messages,
        { id: nextId++, speaker: 'mion', message: response.data.response }
      ]);
      // console.log(response.data.response);
    });
  }

  function scroll() {
    const chat = document.getElementById('chat');
    if (chat) chat.scrollTop = chat.scrollHeight;
  }

  const input = useRef(null);

  function setListener() {
    input.current.id = 'input';
    document
      .getElementById('input')
      .addEventListener('keypress', function (event) {
        if (
          event.key === 'Enter' &&
          this.value !== null &&
          this.value.trim() !== ''
        ) {
          event.preventDefault();
          document.getElementById('btn').click();
        }
      });
  }

  useEffect(() => {
    setListener();
  }, []);

  useEffect(() => {
    scroll();
    // setDummy();
  }, [messages]);

  function setDummy() {
    const chat = document.getElementById('chat');
    if (chat && height <= chat.scrollHeight) {
      height = chat.scrollHeight;
    }
    if (chat && height <= chat.offsetHeight * 2) {
      return <div className={styles.dummy}></div>;
    }
  }

  return (
    <div className={styles.chat}>
      <div className={styles.chatScroller} id="chat">
        {setDummy()}
        {/* <div className={styles.dummy}></div> */}
        {messages.map((message) => (
          <ChatRow
            key={message.id}
            speaker={message.speaker}
            text={message.message}
          />
        ))}
        {/* {console.log(document.getElementById('chat').scrollHeight)} */}
        {/* {window.setInterval(() => {
          scroll();
        }, 100)} */}
      </div>
      <div className={styles.inputBar}>
        <div className={styles.inputField}>
          <TextField
            onChange={handleChange}
            value={value}
            type="text"
            size="l"
            width="full"
            // className={styles.inputTextField}
            placeholder="Спроси у МИОНа"
            form="default"
            inputRef={input}
            rightSide={IconSendMessage}
          ></TextField>
        </div>
        <div className={styles.sendButton}>
          <Button
            size="l"
            iconRight={IconSendMessage}
            onlyIcon
            onClick={sendRequest}
            view="clear"
            id="btn"
          />
        </div>
      </div>
    </div>
  );
};
